import "./index.css";
import UIComponent from "../../../common/UIComponent";

export default class SubtitleList extends UIComponent {
  constructor(elementId, interfaces, currentSubtitle, subtitleList) {
    super();
    this.interfaces = interfaces;
    this.elementId = elementId;
    this.currentSubtitle = currentSubtitle;
    this.subtitleList = subtitleList;
    this.init();
  }

  init() {
    if (this.interfaces.isLive) {
      return;
    }
    this.subtitleListBox = document.createElement("div");
    this.subtitleListBox.setAttribute("class", "vhallPlayer-subtitleList-box");
    this.subtitleListBox.onmouseleave = this.handleMouseLeave.bind(this);

    this.subtitleListTitle = document.createElement("div");
    this.subtitleListTitle.setAttribute("class", "vhallPlayer-subtitleList-title");
    this.subtitleReturnIcon = document.createElement("span");
    this.subtitleReturnIcon.setAttribute("class", "vhallPlayer-subtitleList-leftIcon")
    this.subtitleReturnIcon.onclick = this.handleClickIcon.bind(this);

    this.subtitleListText = document.createElement("span");
    this.subtitleListText.setAttribute("class", "vhallPlayer-subtitleList-titleText");
    this.subtitleListText.innerText = '更多字幕选择';
    this.subtitleListTitle.appendChild(this.subtitleReturnIcon);
    this.subtitleListTitle.appendChild(this.subtitleListText);

    this.subtitleListbody = document.createElement("div");
    this.subtitleListbody.setAttribute("class", "vhallPlayer-subtitleList-body");

    this.subtitleListBox.appendChild(this.subtitleListTitle);
    this.subtitleListBox.appendChild(this.subtitleListbody);
    this.appendChild(this.elementId, this.subtitleListBox);

    for (let i = 0; i < this.subtitleList.length; i += 1) {
      const info = this.subtitleList[i];
      this.list = document.createElement("div");
      this.list.setAttribute("class", "vhallPlayer-subtitleList-item");

      this.listLeft = document.createElement("span");
      this.listLeft.setAttribute("class", "vhallPlayer-subtitleList-left");
      this.listLeft.setAttribute('data-i', `${i}`);

      this.listRight = document.createElement("span");
      this.listRight.setAttribute("class", "vhallPlayer-subtitleList-right");

      this.listLeft.innerText = `字幕文件${i + 1} - ${info.lang}`;
      if (info.index === this.currentSubtitle.index) {
        this.list.classList.add("selected");
        this.selectedIndex = i;
      }
      this.listLeft.onclick = this.handleClickItem.bind(this);
      this.list.appendChild(this.listLeft);
      this.list.appendChild(this.listRight);
      this.subtitleListbody.appendChild(this.list);
    }
  }

  handleMouseLeave() {
    this.subtitleListBox.style.display = 'none';
  }

  handleClickIcon(e) {
    e.stopPropagation();
    const subtitleListBox = document.querySelector(".vhallPlayer-subtitleList-box");
    subtitleListBox.style.display = 'none'
    const configBox = document.querySelector(".vhallPlayer-config-box");
    configBox.style.display = 'inline-block';
    const currEl = document.querySelector(".vhallPlayer-current-subtitle");
    currEl.innerText = `字幕文件${this.currentSubtitle.index + 1} - ${this.currentSubtitle.lang}`;
  }

  handleClickItem(e) {
    e.stopPropagation;
    const el = e.target;
    const index = el.getAttribute("data-i");
    if (Number(index) === this.selectedIndex) {
      return;
    }
    const oldEl = document.querySelector(".vhallPlayer-subtitleList-item.selected");
    oldEl.classList.remove("selected");
    el.parentNode.classList.add('selected');
    this.selectedIndex = Number(index);
    const newSubtitle = this.subtitleList[Number(index)];
    this.currentSubtitle = newSubtitle;
    this.interfaces.switchSubtitle(newSubtitle);
  }
}