const LIVE_CODE = {
  Start: 102001,
  Info: 102002,
  Resume: 102004,
  Pause: 102005,
  Error: 104001,
  // 触发播放
  TouchPlay: 102009
};

/**
 * 直播日志上报
 *
 * @export
 * @class LiveReporter
 * @extends {AbstractReporter}
 */
export default class LiveReporter extends AbstractReporter {
  constructor() {
    super();
    this.isPaused = false;
  }

  __ready() {
    super.__ready();
    this.fire(LIVE_CODE.Start, {
      errcode: '2001'
    });
  }

  __touchPlay() {
    super.__touchPlay();
    this.fire(LIVE_CODE.TouchPlay, {
      errcode: '2006'
    });
  }

  __play(e) {
    super.__play(e);
    if (this.isPaused) {
      this.fire(LIVE_CODE.Resume, {
        errcode: '2004'
      });
    }
  }

  __pause(e) {
    super.__pause(e);
    this.isPaused = true;
    this.fire(LIVE_CODE.Pause, {
      errcode: '2005'
    });
  }

  __error(e) {
    super.__error(e);
    this.fire(LIVE_CODE.Error, {
      si: '',
      errcode: '4001'
    });
  }
  infoPack() {
    // 数据修复，避免出现29998、59999之类的数据
    let duration = this._playInfoDuration / 1000;
    this._playInfoDuration = duration.toFixed() * 1000;
    // 屏蔽由于部分手机计时器不准确，导致出现播放时长超过心跳时长的情况
    this._playInfoDuration =
      this._playInfoDuration > this.reportInterval ? this.reportInterval : this._playInfoDuration;

    let obj = {};
    obj.si = '';
    obj.bt = this._bt;
    obj.errCode = this.player.getReadyState() === 4 ? 2002 : 4001;
    obj.tt = this._playInfoDuration;
    obj.bc = this._lagCount;
    obj.tf = this.httpDownloadSize + this.p2pDownloadSize;

    this.info('info', '信息包内容：', JSON.stringify(obj));
    this.fire(LIVE_CODE.Info, obj);

    this._bt = 0;
    this._lagCount = 0;
    super.infoPack();
  }

  destroy() {
    this.infoPack();
    super.destroy();
  }
}
