import Log from '../../utils/Log';
import Component from './../../core/Component';
import { KV } from './../../core/Constant';
import { PlayerEvent } from './../../PlayerEvents';
import Http from './Http';

const CALC_PLAY_TIME_INTERVAL = 200;
/**
 * 抽象日志发送类
 *
 * @export
 * @class AbstractReporter
 */
export default class AbstractReporter extends Component {
  constructor() {
    super();
    this.url = '';
    this.body = {};
    this.enable = false;
    this.allConfig = null;
    this._config = null;
    this._player = null;
    this.running = false;

    /**
     *
     * @description 心跳计时器 - 上报观看信息包 定时器。
     * @type {number | any}
     *
    */
    this._infoPackInterval = 0;

    /**
     * @description 计算观看时长定时器 - 上报观看时长 tt 用的
     * @type {number | any}
     * */
    this._playTimeInterval = 0;

    /**
     * @description 上报次数
     * @type {number}
     *  */
    this._infoPackCount = 0; // 24小时直播需要发 60*60*24/30

    // this._playHeartbeatDuration = 0;
    this._playInfoDuration = 0;
    // session重置计数
    this._resetSessionCount = 0;

    // 心跳日志发送时间戳（更换session_id使用）
    this.heartBeatTimestamp = Date.now();

    /** 卡顿次数 */
    this._lagCount = 0;

    /** 卡顿时长 */
    this._bt = 0;

    /** 上一个信息包下载量 */
    this._lastInfoDownloadSize = 0;

    this._lastP2PDownloadSize = 0;

    /**
     * @description 上一次计算播放时长的 - 相对时间
     * @type {number}
    */
    this._lastCalcPlayTime = Date.now();

    this.reportInterval = 30 * 1000;
  }

  setReportConfig(reportConfig, otherOption) {
    if (typeof reportConfig === 'string') {
      reportConfig = JSON.parse(reportConfig);
    }
    if (typeof otherOption === 'string') {
      otherOption = JSON.parse(otherOption);
    }

    this._config = reportConfig;
    let cfg = this._config;
    this.url = cfg.url;
    this.enable = cfg.enable === undefined ? true : cfg.enable;
    let pf = 7;
    let df = 'web';
    if (this.player) {
      let context = this.player.context;
      pf = context.platform === 'pc' ? 7 : 10;
      df = context.platform === 'pc' ? 'web' : 'wap';
    }
    this.body.pf = pf;
    this.body.df = df;
    this.body.osv = this.getOS();
    this.body.ua = navigator.userAgent;
    this.body.p = cfg.webinar_id || '';
    this.body.aid = cfg.webinar_id || '';
    this.body.uid = cfg.uid || '';
    this.body.s = cfg.session_id || '';
    this.body.vid = cfg.vid || '';
    this.body.vfid = cfg.vfid || '';
    this.body.ndi = cfg.ndi || '';
    this.body.guid = cfg.guid || '';
    this.body.vtype = cfg.vtype || '';
    this.body.topic = cfg.topic || '';
    this.body.app_id = cfg.app_id || '';
    this.body.flow_type = cfg.flow_type || '';
    this.body.bu = cfg.bu;
    //
    this.body.pushTime = Date.now();
    this.body.protocol = this.allConfig.type || '';
    this.body.videoWidth = 480;
    this.body.videoHeight = 854;
    // 获取默认播放清晰度
    if (this.allConfig['plugin_scheduler']) {
      let _schedulerConfig = JSON.parse(this.allConfig['plugin_scheduler']);
      this.body.picture_quality = _schedulerConfig.defaultDef || '480p';
    }
    // 组合自定义上报字段
    Object.assign(this.body, otherOption);

    let interval = parseInt(cfg.interval) <= 0 ? 1 : +cfg.interval;
    this.reportInterval = interval * 1000 || 30 * 1000;
    this.__ready();
  }

  set player(p) {
    this._player = p;
    this._handleCareEvent();
  }

  get player() {
    return this._player;
  }

  get httpDownloadSize() {
    let downloadSize = this.player.getDownloadSize();
    let tf = downloadSize - this._lastInfoDownloadSize;
    this._lastInfoDownloadSize = downloadSize;
    return tf;
  }

  get p2pDownloadSize() {
    if (window.peer5) {
      let stats = window.peer5.getStats();
      let downloadSize = stats ? stats.totalP2PDownloaded : 0;
      let tf = downloadSize - this._lastP2PDownloadSize;
      this._lastP2PDownloadSize = downloadSize;
      return tf;
    } else {
      return 0;
    }
  }

  _handleCareEvent() {
    this.player.on(PlayerEvent.PLAY, this.__play.bind(this));
    this.player.on(PlayerEvent.PAUSE, this.__pause.bind(this));
    this.player.on(PlayerEvent.PLAY_END, this.__ended.bind(this));
    this.player.on('lagreport', this.__lag.bind(this));
    this.player.on('lagrecover', this.__lagRecover.bind(this));
    this.player.on('bufferempty', this.__bufferEmpty.bind(this));
    // this.player.on('bufferfull', this.__bufferFull.bind(this));
    this.player.on('error', this.__error.bind(this));
    this.player.on(PlayerEvent.SRC_CHANGED, this.__srcChange.bind(this));
    // this.player.once(PlayerEvent.READY, this.__ready.bind(this));
    this.player.on(PlayerEvent.OVER, this.__over.bind(this));
    this.player.on(PlayerEvent.LOADEDMETADATA, this.__loadedmetadata.bind(this));
    this.player.on(PlayerEvent.DEFINITION_CHANGED, this.__definitionchange.bind(this));
    this.player.on(PlayerEvent.TOUCH_PLAY, this.__touchPlay.bind(this));
  }

  __loadedmetadata(e) {
    // 获取视频分辨率
    if (e.target) {
      this.body.videoWidth = e.target.videoWidth || 0;
      this.body.videoHeight = e.target.videoHeight || 0;
    }
  }

  __definitionchange(e) {
    if (e.newValue && e.newValue.def) {
      this.body.picture_quality = e.newValue.def;
    }
  }

  __touchPlay() { }

  __play(e) {
    if (!this.running) {
      this.start();
    }
    if (!this._playTimeInterval) {
      this._lastCalcPlayTime = Date.now();
      this._playTimeInterval = setInterval(this.calcPlayTime.bind(this), CALC_PLAY_TIME_INTERVAL);
    }
  }

  __pause(e) {
    // 暂停状态 -- 保持上报心跳包
    // if (this.running) {
    //   // this.stop();
    // }

    if (this._playTimeInterval) {
      clearInterval(this._playTimeInterval);
      this._playTimeInterval = 0;
    }
  }

  __ended(e) {
    // 播放完毕的时候，就不在进行心跳了
    this.stop();
  }

  __over(b) {
    if (b) {
      this.stop();
    }
  }

  __lag(e) {
    this._lagCount += 1;
    // 停止记录播放时长
    if (this._playTimeInterval) {
      clearInterval(this._playTimeInterval);
      this._playTimeInterval = 0;
    }
  }

  __lagRecover(t) {
    this.info('info', `卡顿恢复了，消耗了${t}ms`);
    this._bt += t;

    // 重新计算播放时长
    if (this._playTimeInterval) {
      clearInterval(this._playTimeInterval);
      this._playTimeInterval = 0;
    }
    this._lastCalcPlayTime = Date.now();
    this._playTimeInterval = setInterval(this.calcPlayTime.bind(this), CALC_PLAY_TIME_INTERVAL);
  }

  __bufferEmpty() {
    clearInterval(this._playTimeInterval);
    this._playTimeInterval = 0;
  }

  __bufferFull(e) {
    // clearInterval(this._playTimeInterval);
    // this._playTimeInterval = 0;
    // this._lastCalcPlayTime = Date.now();
    // // 循环记录播放时长
    // this._playTimeInterval = setInterval(this.calcPlayTime.bind(this), CALC_PLAY_TIME_INTERVAL);
  }

  __ready() { }

  __error(e) { }

  __srcChange(e) {
    if (e.oldValue === '') {
      return;
    }
    this.info('info', `换线了，${e}`);
    clearInterval(this._infoPackInterval);
    this._infoPackInterval = 0;
    clearInterval(this._playTimeInterval);
    this._playTimeInterval = 0;
    this.running = false;
    // 发送信息报、重置一些状态、记录时间点
    this._infoPackCount = 0;
    this.infoPack();
    this._lagCount = 0;
    this._infoPackCount = 0;
    // this._playHeartbeatDuration = 0;
    this._playInfoDuration = 0;
    this._lastInfoDownloadSize = 0;
  }

  start() {
    if (this.running) {
      return;
    }

    this.info('info', '启动日志上报');
    this.running = true;
    // this._playHeartbeatDuration = 0;
    this._playInfoDuration = 0;
    clearInterval(this._infoPackInterval);
    this._infoPackInterval = 0;
    clearInterval(this._playTimeInterval);
    this._playTimeInterval = 0;

    this._infoPackInterval = setInterval(this.infoPack.bind(this), this.reportInterval);
    this._playTimeInterval = setInterval(this.calcPlayTime.bind(this), CALC_PLAY_TIME_INTERVAL);
    this._lastCalcPlayTime = Date.now();
  }

  stop() {
    this.info('info', '停止日志上报');
    this.running = false;
    this._playInfoDuration = 0;
    // this._playHeartbeatDuration = 0;
    clearInterval(this._infoPackInterval);
    this._infoPackInterval = 0;
    clearInterval(this._playTimeInterval);
    this._playTimeInterval = 0;
  }

  /**
   * 销毁
   *
   * @memberof AbstractReporter
   */
  destroy() {
    super.destroy();
    this._playInfoDuration = 0;
    // this._playHeartbeatDuration = 0;
    clearInterval(this._infoPackInterval);
    this._infoPackInterval = 0;
    clearInterval(this._playTimeInterval);
    this._playTimeInterval = 0;

    this._infoPackCount = 0;
    this._lagCount = 0;
    this.Info = 0;

    this._lastInfoDownloadSize = 0;
    // this._lastHeartbeatDownloadSize = 0;

    this._lastCalcPlayTime = Date.now();
  }

  /**
   * 信息包
   *
   * @memberof AbstractReporter
   */
  infoPack() {
    this._infoPackCount += 1;
    this._playInfoDuration = 0;
    this.heartBeatTimestamp = Date.now();
  }

  calcPlayTime() {
    const now = Date.now();
    const elapsedTime = now - this._lastCalcPlayTime;
    //
    this._playInfoDuration += elapsedTime;
    // 计算60s 心跳包 -- 时间 取消掉
    // this._playHeartbeatDuration += elapsedTime;
    this._lastCalcPlayTime = now;
  }

  fire(code, obj = {}) {
    if (!this.url) {
      this.info('error', 'report domain is null');
      return;
    }

    // 超过30分钟未上报心跳日志，更换session
    if (Date.now() - this.heartBeatTimestamp >= 1800 * 1000) {
      this.heartBeatTimestamp = Date.now();
      this._resetSessionCount += 1;
      this.body.s = this._config.session_id + '_' + this._resetSessionCount.toString();
    }

    this.body.ver = this.player.version;
    // 播放地址
    let el = this.getURLInfo();
    this.body.sd = el.host;
    this.body.fd = el.pathname;
    // 发送时间戳
    this.body.pushTime = Date.now();
    Object.assign(obj, this.body);

    let param = {
      k: code,
      id: Date.now(),
      s: this.body.s,
      bu: this.body.bu,
      token: btoa(JSON.stringify(obj))
    };

    if (this.enable) {
      let http = new Http();
      // http.onError = e => this.info('error', 'could not report')
      http.onTimeout = e => this.info('error', 'report timeout');
      http.fire(this.url, param);
      this.info('info', `日志参数列表k: ${code}`);
    } else {
      this.info('info', `日志上报配置文件[enable:false],不进行日志上报, code:${code}`);
    }

    // Log.OBJ.table(obj);
  }

  /**
   * 返回Location对象
   *
   * @memberof Reporter
   * @returns Location 对象
   */
  getURLInfo() {
    const { store } = this.player;
    let url = store.getKV(KV.URL);
    if (!this.el) {
      this.el = document.createElement('a');
    }
    let el = this.el;
    el.href = url;
    return el;
  }

  /**
   * 获取操作系统版本
   * @returns {string}
   */
  getOS() {
    let sUserAgent = navigator.userAgent;
    let isWin = (navigator.platform === 'Win32') || (navigator.platform === 'Windows');
    let isMac = (navigator.platform === 'Mac68K') || (navigator.platform === 'MacPPC') ||
      (navigator.platform === 'Macintosh') || (navigator.platform === 'MacIntel');
    if (isMac) return 'Mac';
    let isUnix = (navigator.platform === 'X11') && !isWin && !isMac;
    if (isUnix) return 'Unix';
    let isLinux = (String(navigator.platform).indexOf('Linux') > -1);
    if (isLinux) return 'Linux';
    if (isWin) {
      let isWin2K = sUserAgent.indexOf('Windows NT 5.0') > -1 || sUserAgent.indexOf('Windows 2000') > -1;
      if (isWin2K) return 'Win2000';
      let isWinXP = sUserAgent.indexOf('Windows NT 5.1') > -1 || sUserAgent.indexOf('Windows XP') > -1;
      if (isWinXP) return 'WinXP';
      let isWin2003 = sUserAgent.indexOf('Windows NT 5.2') > -1 || sUserAgent.indexOf('Windows 2003') > -1;
      if (isWin2003) return 'Win2003';
      let isWinVista = sUserAgent.indexOf('Windows NT 6.0') > -1 || sUserAgent.indexOf('Windows Vista') > -1;
      if (isWinVista) return 'WinVista';
      let isWin7 = sUserAgent.indexOf('Windows NT 6.1') > -1 || sUserAgent.indexOf('Windows 7') > -1;
      if (isWin7) return 'Win7';
      let isWin10 = sUserAgent.indexOf('Windows NT 10') > -1 || sUserAgent.indexOf('Windows 10') > -1;
      if (isWin10) return 'Win10';
    }
    return 'other';
  }
}
