import Plugin from '../core/Plugin';
import TencentInject from './lag/TencentInject';
import { PlayerEvent } from '../PlayerEvents';

/**
 * 卡顿插件
 *
 * @export
 * @class Lag
 * @extends {Plugin}
 */
export default class Lag extends Plugin {
  constructor() {
    super();
    this._readyStateChecking = false;
    this._readyStateInterval = 0;
    this._lastLagTime = 0;
    this._tlag = null;
  }

  init(opts = {}) {
    super.init(opts);
    this._allConfig = opts;
    this._handleCareEvent();
    this._tlag = new TencentInject();
  }

  destroy() {
    super.destroy();

    this._readyStateChecking = false;
    this._lastLagTime = 0;
    clearInterval(this._readyStateInterval);
    if (this._tlag) {
      this._tlag.destroy();
      this._tlag = null;
    }
    // this.player.off('play', this.__play);
    // this.player.off('pause', this.__pause);
    // this.player.off('waiting', this.__waiting);
    // this.player.off('ended', this.__ended);
  }

  static get type() {
    return 'plugin_lag';
  }

  _handleCareEvent() {
    this.player.on('play', this.__play.bind(this));
    this.player.on('pause', this.__pause.bind(this));
    // this.player.on('waiting', this.__waiting.bind(this))
    this.player.on('ended', this.__ended.bind(this));
    this.player.on(PlayerEvent.OVER, this.__over.bind(this));
    this.player.on(PlayerEvent.DEFINITION_CHANGED, this.__definitionChanged.bind(this));
    this.player.on(PlayerEvent.ERROR, this.__error.bind(this));
    this.player.on(PlayerEvent.STALLED, this.__error.bind(this));
  }

  __play(e) {
    this.__checkReadyState();
  }

  __pause(e) {
    this.__stopCheckReadyState();
  }
  // __waiting(e) {
  //   // waiting事件，据说在手机H5上不好使？！
  //   this.__checkReadyState()
  // }
  __ended(e) {
    this.__stopCheckReadyState();
  }

  __over(b) {
    if (b) {
      this.__stopCheckReadyState();
    }
  }

  __definitionChanged(e) {
    // 接收到切清晰度后，停止卡顿判断
    this.__stopCheckReadyState();
  }

  __error(e) {
    // let error = this.player.player.video.error;
    let error = e.target && e.target.error;
    if (error) this.info('warn', `Player Error: code => ${error.code}; details => ${error.message}`);
    this.info('info', '播放器报错', e);
    // 为了方便阅读，分开写判断
    // MSE的HLS情况下，拖动进度条报错不予开启
    if (e && e.type === 'mediaError' && (e.details === 'bufferStalledError' || e.details === 'bufferNudgeOnStall')) {
      this.info('info', '忽略');
      return;
    }
    // safari浏览器播放某些HLS视频时，未播放的情况下也报此错误。故不开启
    if (e && e.target && e.type === 'stalled') {
      this.info('info', '忽略');
      return;
    }

    this.__checkReadyState();
  }

  __checkReadyState() {
    if (this._readyStateChecking) {
      return;
    }

    this._readyStateChecking = true;
    clearInterval(this._readyStateInterval);
    // 设置当前时间，切线后可能会获取的当前时间和之前保存的不一致，导致误判卡顿恢复
    // 需要增加延时（小于判断卡顿的延时），不然获取的当前时间还是之前的。。。。
    setTimeout(() => { this._tlag.setLastTime(this.player.getCurrentTime()); }, 300);
    // 每500ms检测一下，卡顿持续时间超过2秒，汇报一次卡顿
    let lagCount = 0;
    this._readyStateInterval = setInterval(() => {
      // 判断是否卡顿，并返回已连续卡顿次数。
      // PaaS版本2.2.2，内核版本2.1.6以后，统一用腾讯系（通用）方式探测卡顿
      lagCount = this._tlag.isLag(this.player.getCurrentTime(), lagCount);
      // 卡顿，并且每卡顿2s（500ms * 4）
      if (lagCount !== 0 && !(lagCount % (this._allConfig.lagThreshold * 2))) {
        // 记录第一次卡顿时间
        if (this._lastLagTime === 0) {
          // 已卡顿2s才记录时间，所以第一次卡顿时间要向前推2s
          this._lastLagTime = Date.now() - (this._allConfig.lagThreshold * 1000);
          this.player.emit2All('bufferempty');
        }
        this.player.emit2All('lagreport');
      } else if (lagCount === 0 && this._lastLagTime !== 0) { // 卡顿恢复（不卡顿，并且有记录卡顿时间）
        // 统计卡顿时长
        let elapsed = Date.now() - this._lastLagTime;
        // 清零上次卡顿时间
        this._lastLagTime = 0;
        this.player.emit2All('lagrecover', elapsed);
      }
    }, 500);
  }

  __stopCheckReadyState() {
    this._readyStateChecking = false;
    clearInterval(this._readyStateInterval);
  }
}
