import {CONFIG} from '../Config'
import {UAParser} from 'ua-parser-js'
import semver from 'semver'

/**
 * 获取文件后缀
 * @param {string} file 文件名
 */
export function getType(file) {
  return file.split('.').pop();
}

/**
 * 加载base
 */
export function loadMsgScript() {
  return new Promise((resolve, reject) => {
    if (document.getElementById("VhallBaseSDK")) {
      resolve(true)
      return;
    }
    let scriptEl = document.createElement('script')
    scriptEl.setAttribute('type', 'text/javascript')
    scriptEl.setAttribute('id', "VhallBaseSDK");
    scriptEl.setAttribute('src', CONFIG.BASE_MESSAGE_URL);
    document.head.appendChild(scriptEl);
    scriptEl.onload = () => resolve(true);
    scriptEl.onerror = () => reject(false);
  });
}

Date.prototype.Format = function (fmt) {
  var o = {
    "M+": this.getMonth() + 1,  //月份
    "d+": this.getDate(),  //日
    "h+": this.getHours(),  //24小时制
    "m+": this.getMinutes(),  //分
    "s+": this.getSeconds(),  //秒
    "q+": Math.floor((this.getMonth() + 3) / 3),  //季度
    "S": this.getMilliseconds()  //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

// 防抖
export function debounce(fn, delay) {
  var timer = null;
  return function () {
    var _this = this;
    var _args = arguments;
    // 清除上一个timer
    clearTimeout(timer);
    // 当最后回调时，经过delay毫秒后执行事件处理程序
    timer = setTimeout(function () {
      fn.apply(_this, _args);
    }, delay);
  }
}

// 节流
export function throttle(fn, delay) {
  var last = 0;
  return function () {
    var cur = +new Date();
    if (cur - last > delay) {
      fn.apply();
      last = cur;
    }
  }
}

export function isMobile() {
  return navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
}

export const systemProbe = function () {
  let result = Object.create(null);
  let uaResult = new UAParser().getResult();
  result.isTencent = (uaResult.ua.match(/mqqbrowser|qzone|qqbrowser|qq|micromessenger/i) || []).length > 0;
  result.browser = uaResult.browser.name.toLowerCase();
  result.browserVersion = uaResult.browser.version;
  result.os = uaResult.os.name.toLowerCase();
  result.osVersion = uaResult.os.version;
  result.platform = (
    uaResult.ua.match(
      /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
    ) || []
  ).length > 0
    ? 'mobile'
    : 'pc';
  // cv short for convert version.
  const cv = ov => semver.coerce(ov);

  // 基于 https://caniuse.com/#search=mediasource 2019-08-23日结果
  const judgeMSE = () => {
    window.semver = semver;

    // iOS safari 完全不支持
    if (result.os === 'ios' && result.browser.indexOf('safari')) {
      return false;
    }

    // chrome 小于23 完全不支持
    if (result.browser === 'chrome' && semver.lt(cv(result.browserVersion), cv(23))) {
      return false;
    }

    // firefox 42以下不支持
    if (result.browser === 'firefox' && semver.lt(cv(result.browserVersion), cv(42))) {
      return false;
    }

    // ie 11 以下不支持(11版本部分支持)
    if (result.browser === 'ie' && semver.lt(cv(result.browserVersion), cv(11))) {
      return false;
    }

    // 安卓4.4以下不支持
    if (result.os === 'android' && semver.lt(cv(result.osVersion), cv(4.4))) {
      return false;
    }

    // opera 15以下不支持
    if (result.browser === 'opera' && semver.lt(cv(result.browserVersion), cv(15))) {
      return false;
    }

    // opera mobile46以下不支持
    if (result.browser === 'opera mobile' && semver.lt(cv(result.browserVersion), cv(46))) {
      return false;
    }

    // opera mini 不支持
    if (result.browser === 'opera mini') {
      return false;
    }

    // 三星不支持
    if (result.browser.indexOf('samsung') >= 0) {
      return false;
    }

    // 如果window上都没有MediaSource不存在，不支持MSE
    if (typeof window !== 'undefined') {
      // 判断是否有mediaSource
      const existMediaSource = window.MediaSource || window.WebKitMediaSource;
      // 判断是否有SourceBuffer
      const sourceBuffer = window.SourceBuffer || window.WebKitSourceBuffer;
      // 判断sourceBuffer 是否有appendBuffer
      const validSourceBufferApi =
        (sourceBuffer &&
          sourceBuffer.prototype &&
          typeof sourceBuffer.prototype.appendBuffer === 'function' &&
          typeof sourceBuffer.prototype.remove === 'function') ||
        false;

      return existMediaSource && validSourceBufferApi;
    }

    return true;
  };
  result.supportMSE = judgeMSE();
  return result;
};

/**
 * 判断浏览器是否支持flash
 */
export function hasUsableSWF() {
  let swf = null;
  if (typeof window.ActiveXObject !== 'undefined') {
    try {
      swf = new ActiveXObject('ShockwaveFlash.ShockwaveFlash');
    } catch (e) {
      return !!swf;
    }
  } else {
    swf = navigator.plugins['Shockwave Flash'];
  }
  return !!swf;
}

export function judgeX5() {
  let ua = navigator.userAgent.toLowerCase();
  let isAndroid = ua.match(/android|adr/i);
  let isTencent = ua.match(/mqqbrowser|qzone|qqbrowser|qq|micromessenger/i);

  return isAndroid && isTencent;
}
