import { Promise } from 'es6-promise';
import PlayerProxy from '../../PlayerProxy';
import RtcCore from "./dist/bundle";

/**
 * Rtc播放器封装
 *
 * @export
 * @class RrcPlayer
 * @extends {RtcPlayer}
 */
export default class RtcPlayer extends PlayerProxy {
    constructor(opt = {}) {
        let RtcCore = RtcCore || window.RtcCore
        super(opt);
        this.TAG = 'RtcPlayer';
        this._type = 'RtcPlayer';
        this.rtc = new RtcCore();
        this.curTotalBytesSent = 0;
        this.curTotalBytesReceived = 0;
        this.curDownloadSizeBytes = 2621440;
        this.remoteStream = new MediaStream();
    }

    getDownloadSize() {
        this.getStatus().then((status) => {
            console.info(status);
            if (status && status.bytesReceived) {
                this.curDownloadSizeBytes = status.bytesReceived + status.bytesSent;
            }
        }).catch((e) => {
            console.error('getDownloadSize error info：', e);
        })
        console.info('rtc received size:', this.curDownloadSizeBytes);
        return this.curDownloadSizeBytes || 0;
    }

    initVideo(option = {}) {
        super.initVideo(option);
        let {liveOption, rtc_schedule_server, accountId, plugin_reporter} = option;
        try{
            plugin_reporter = JSON.parse(option.plugin_reporter)
        }catch (e) {
            console.error('plugin_reporter JSON format error');
        }
        if (!accountId) {
            console.error('Lack of key parameter - accountId')
            return;
        }
        if (!liveOption.inavId) {
            console.error('Lack of key parameter - invaId')
            return;
        }
        if (!rtc_schedule_server) {
            console.error('Service exception Key RTC parameters are missing.')
            return;
        }
        let streamId = `webrtc://vrtc.liveplay.vhall.com?roomId=${liveOption.roomId}&inavId=${liveOption.inavId}&userSessionId=${accountId}&streamType=2`;
        this.rtc.init({...option, streamId, loggerParam: plugin_reporter}).then(() => {
            this.rtc.connect(rtc_schedule_server).then(() => {
                //TODO...
            }).catch((e) => {
                console.error(e);
            })
        }).catch((e) => {
            console.error(e);
        })
        this.rtc.on('track', (stream) => {
            if (stream.track.kind === 'audio') {
                this.remoteStream.getAudioTracks().forEach((i) => {
                    this.remoteStream.removeTrack(i);
                })
            } else if (stream.track.kind === 'video') {
                this.remoteStream.getVideoTracks().forEach((i) => {
                    this.remoteStream.removeTrack(i);
                })
            }
            this.remoteStream.addTrack(stream.track);
            super.setSrcObject(this.remoteStream);
        })
        this.rtc.on('media-disconnect', () => {
            this.curTotalBytesSent = 0;
            this.curTotalBytesReceived = 0;
        })
        if (option.store) {
            const {store} = option;
            this.playedTime = 0;
        }
    }

    getStatus() {
        return new Promise(async (res, rej) => {
            try {
                const status = await this.rtc.getStats(30);
                const streamDetail = {};
                status.transport.forEach((stat) => {
                    if (stat.bytesReceived) {
                        const bytesReceived = stat.bytesReceived - this.curTotalBytesReceived;
                        streamDetail.bytesReceived = bytesReceived > 0 ? bytesReceived : 0;
                        this.curTotalBytesReceived = stat.bytesReceived;
                        streamDetail.totalBytesReceived = stat.bytesReceived;
                    }
                    if (stat.bytesSent) {
                        const bytesSent = stat.bytesSent - this.curTotalBytesSent;
                        streamDetail.bytesSent = bytesSent > 0 ? bytesSent : 0;
                        this.curTotalBytesSent = stat.bytesSent;
                        streamDetail.totalBytesSent = stat.bytesSent;
                    }
                    streamDetail.bitRate = (8 * (streamDetail.bytesReceived + streamDetail.bytesSent)) / 30;
                });

                status.track.forEach((stat) => {
                    if (stat.frameHeight && stat.frameWidth) {
                        streamDetail.videoWidth = stat.frameWidth;
                        streamDetail.videoHeight = stat.frameHeight;
                    }
                });
                res(streamDetail)
            } catch (e) {
                rej(e)
            }
        })
    }

    setSrc() {
        this.playedTime = this.getCurrentTime();
        this.playBackRate = this.getPlaybackRate();
        return new Promise((resolve, reject) => {
            this.video.onloadeddata = () => {
                if (!this.getIsLive() && this._setSrcCount !== 1) {
                    this.setCurrentTime(this.playedTime);
                    this.setPlaybackRate(this.playBackRate);
                }
                resolve();
                this.video.play();
            };
            let err = setTimeout(() => {
                reject(new Error('fail'));
                clearTimeout(err);
                err = null;
            }, 3000);
        });
    }
}
