import AbstractReporter from './AbstractReporter';

const VOD_CODE = {
  /** 初始化完成 */
  Start: 92001,
  Pause: 92002,
  /** 心跳 */
  // HeartBeat: 92003,
  Resume: 92004,
  Info: 92005,
  /** 卡顿 */
  Lag: 94001,
  // 触发播放
  TouchPlay: 92009,
  Destory: 92006 // 播放器停止
};

/**
 * 点播日志上报
 *
 * @export
 * @class LiveReporter
 * @extends {AbstractReporter}
 */
export default class VodReporter extends AbstractReporter {
  constructor() {
    super();
    this._lastInfoBt = 0;
    this.isPaused = false;
  }

  destroy() {
    this.fire(VOD_CODE.Destory, {
      errcode: '2006'
    });
    super.destroy();
    this._lastInfoBt = 0;
  }

  __touchPlay() {
    // this.isPaused = false;
    super.__touchPlay();
    this.fire(VOD_CODE.TouchPlay, {
      errcode: '2006'
    });
  }

  __play(e) {
    super.__play(e);
    if (this.isPaused) {
      this.isPaused = false;
      this.fire(VOD_CODE.Resume, {
        errcode: '2004'
      });
    } else {
      this.info('info', '正常点击播放--不进行恢复播放的上报！');
    }
  }

  __pause(e) {
    this.isPaused = true;
    this.fire(VOD_CODE.Pause, {
      errcode: '2002'
    });
    super.__pause(e);
  }

  /**
   *  @description 播放结束 - 手动触发一次。心跳包上报。
  */
  __ended() {
    this.infoPack();
    super.__ended();
  }

  __over() {
    this.infoPack();
    super.__over();
  }

  __ready() {
    super.__ready();
    this.fire(VOD_CODE.Start, {
      errcode: '2001'
    });
  }

  infoPack() {
    // 数据修复，避免出现29998、59999之类的数据
    let duration = this._playInfoDuration / 1000;
    this._playInfoDuration = duration.toFixed() * 1000;
    // 屏蔽由于部分手机计时器不准确，导致出现播放时长超过心跳时长的情况
    this._playInfoDuration =
      this._playInfoDuration > this.reportInterval ? this.reportInterval : this._playInfoDuration;

    let obj = {};
    obj.errcode = '2005';
    obj.tt = this._playInfoDuration;
    obj.bc = this._lagCount;
    obj.bt = this._bt;
    obj.tf = this.httpDownloadSize + this.p2pDownloadSize;

    this.info('info', '信息包内容：', JSON.stringify(obj));
    this.fire(VOD_CODE.Info, obj);

    this._lastInfoBt = this._bt;
    this._lagCount = 0;
    this._bt = 0;
    super.infoPack();
  }
}
