import Plugin from '../core/Plugin';
import axios from 'axios';
import { appendChild, createElement, removeFromParent, addStyle } from '../utils/Dom';
import { PlayerEvent } from './../PlayerEvents';
import { isMobile } from '../utils/util';

/**
 * 字幕插件
 *
 * @export
 * @class Subtitle
 * @extends {Plugin}
 * @author yaqian.hu
 */
export default class Subtitle extends Plugin {
  constructor() {
    super();
    this.CLASS_NAME = this.constructor.name;
    this.currentSubtitle = null;
    this.srtMap = [];
  }

  init(opts = {}) {
    super.init(opts);
    this._allConfig = opts;
    this._subtitleCfg = JSON.parse(opts[Subtitle.type]);
    let subtitleCfg = this._subtitleCfg;
    this._defineMethods();

    if (opts.isLive) {
      return;
    }
    if (subtitleCfg.enable === false) {
      return;
    }
    const subtitleObj = subtitleCfg.subtitle_info;
    this._fetchSubtitle(subtitleObj);
    this._createSubtitle(subtitleCfg);
    this._handleCareEvent();
  }

  _defineMethods() {
    this.player.openSubtitle = this._openSubtitle.bind(this);
    this.player.closeSubtitle = this._closeSubtitle.bind(this);
    this.player.getCurrentSubtitle = this._getCurrentSubtitle.bind(this);
    this.player.getSubtitleList = this._getSubtitleList.bind(this);
    this.player.switchSubtitle = this._switchSubtitle.bind(this);
  }

  _handleCareEvent() {
    this.player.on('fullscreenchanged', this._fullscreenChanged.bind(this));
  }

  // 获取字幕文件并解析
  _fetchSubtitle(subtitleObj) {
    if (subtitleObj && subtitleObj['srt']) {
      const srtInfoArr = subtitleObj['srt'];
      this.srtMap = srtInfoArr;
      // 获取字幕文件并解析
      for (let i = 0; i < srtInfoArr.length; i += 1) {
        const srtInfo = srtInfoArr[i];
        srtInfo['index'] = i;
        srtInfo['isDefault'] = srtInfo['is_default'] === 1;
        delete srtInfo['is_default'];
        if (srtInfo['isDefault'] || i === 0) {
          this.currentSubtitle = srtInfo;
        }
        if (srtInfo['isDefault']) {
          this.defaultSrt = srtInfo;
        }
        const url = srtInfo['url'];
        this.downloadFile(url, i).then((res) => {
          if (this.currentSubtitle && this.currentSubtitle.index === res.index) {
            this.currentSubtitle.text = res.subtitle;
          }
          if (this.defaultSrt && this.defaultSrt.index === res.index) {
            this.defaultSrt.text = res.subtitle;
          }
          this.srtMap[res.index]['text'] = res.subtitle;
        }, (res) => {
          // 失败处理
        }
        );
      }
    }
  }

  // 创建字幕区域
  _createSubtitle(subtitleCfg) {
    this.subtitleContainer = createElement('div', { className: 'vh-subtitle' });
    this.subtitleGroup = createElement('div', { className: 'subtitle-group' });
    this.subtitleContainer.appendChild(this.subtitleGroup);
    appendChild(this._allConfig['id'], this.subtitleContainer);
    let styleSheet = null;
    let styleSheet2 = null;
    if (!isMobile()) {
      styleSheet = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        width: '72.5%',
        height: '12.5%',
        maxHeight: '83%',
        pointerEvents: 'none',
        position: 'absolute',
        left: '13.75%',
        bottom: '4.5%',
        zIndex: '1'
      };
      styleSheet2 = {
        display: 'inline-block',
        margin: '0 4px',
        fontSize: '16px',
        textShadow: '#333338 1px 0 0, #333338 0 1px 0, #333338 -1px 0 0, #333338 0 -1px 0',
        color: '#FFFFFF',
        wordWrap: 'break-word'
      };
    } else {
      styleSheet = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        width: '80%',
        height: '16%',
        maxHeight: '80%',
        pointerEvents: 'none',
        position: 'absolute',
        left: '10%',
        bottom: '6%',
        zIndex: '1'
      };
      styleSheet2 = {
        display: 'inline-block',
        margin: '0 2px',
        fontSize: '12px',
        textShadow: '#333338 1px 0 0, #333338 0 1px 0, #333338 -1px 0 0, #333338 0 -1px 0',
        color: '#FFFFFF',
        wordWrap: 'break-word'
      };
    }
    addStyle(this.subtitleContainer, styleSheet);
    addStyle(this.subtitleGroup, styleSheet2);

    // 字幕配置设为自动显示时
    if (subtitleCfg['auto']) {
      // 开启字幕
      this._openSubtitle();
    }
  }

  // 字幕与时间同步显示
  _syncRender(e) {
    if (this.srtMap.length === 0 || !this.player.isOpenSubtitle) {
      return;
    }
    const currentTime = this.player.getCurrentTime();
    if (!this.currentSubtitle) {
      this.currentSubtitle = this.defaultSrt || this.srtMap[0];
    }
    const subtitleArr = this.currentSubtitle.text || [];
    for (let i = 0; i < subtitleArr.length; i += 1) {
      if (currentTime < subtitleArr[0].startTime || currentTime > subtitleArr[subtitleArr.length - 1].endTime) {
        this.subtitleGroup.innerHTML = '';
        break;
      }
      const item = subtitleArr[i];
      if (currentTime >= item.startTime && currentTime <= item.endTime) {
        this.subtitleGroup.innerHTML = `<span style='display: inline-block; text-align: center'>${item.content}</span>`;
        break;
      } else {
        this.subtitleGroup.innerHTML = '';
      }
    }
  }

  // 开启字幕
  _openSubtitle() {
    this.player.isOpenSubtitle = true;
    this.player.emit2All(PlayerEvent.OPEN_SUBTITLE);
    this.player.on(PlayerEvent.TIMEUPDATE, this._syncRender.bind(this));
  }

  // 关闭字幕
  _closeSubtitle() {
    this.player.isOpenSubtitle = false;
    this.player.off(PlayerEvent.TIMEUPDATE, this._syncRender.bind(this));
    this.player.__ee__.timeupdate.forEach((item, index) => {
      if (item.name === 'bound _syncRender') {
        this.player.__ee__.timeupdate.splice(index, 1);
      }
    });
    if (this.subtitleGroup.innerHTML) {
      this.subtitleGroup.innerHTML = '';
    }
    this.player.emit2All(PlayerEvent.CLOSE_SUBTITLE);
  }

  // 获取当前字幕信息
  _getCurrentSubtitle() {
    return this.currentSubtitle;
  }

  // 获取字幕列表
  _getSubtitleList() {
    return this.srtMap;
  }

  // 切换字幕
  _switchSubtitle(obj) {
    const e = {
      oldValue: JSON.parse(JSON.stringify(this.currentSubtitle)),
      newValue: obj
    };
    this.currentSubtitle = obj;
    this.player.emit2All(PlayerEvent.SUBTITLE_CHANGED, e);
  }

  /**
   * 字体大小随屏幕变化
   */
  _fullscreenChanged(e) {
    if (!isMobile()) {
      if (e === 'normal') {
        const normalStyle = {
          margin: '0 4px',
          fontSize: '16px',
          textShadow: '#333338 1px 0 0, #333338 0 1px 0, #333338 -1px 0 0, #333338 0 -1px 0'
        };
        addStyle(this.subtitleGroup, normalStyle);
      } else if (e === 'fullscreen') {
        const fullscreenStyle = {
          margin: '0 8px',
          fontSize: '32px',
          textShadow: '#333338 2px 0 0, #333338 0 2px 0, #333338 -2px 0 0, #333338 0 -2px 0'
        };
        addStyle(this.subtitleGroup, fullscreenStyle);
      }
    } else {
      if (e === 'normal') {
        const normalStyle = {
          margin: '0 2px',
          fontSize: '12px'
        };
        addStyle(this.subtitleGroup, normalStyle);
      } else if (e === 'fullscreen') {
        const fullscreenStyle = {
          margin: '0 4px',
          fontSize: '14px'
        };
        addStyle(this.subtitleGroup, fullscreenStyle);
      }
    }
  }

  // 下载字幕文件
  downloadFile(url, index) {
    return new Promise((resolve, reject) => {
      axios.get(`${url}`).then(res => {
        // 解析srt字幕文件
        const subtitle = this.parseSrtSubtitle(res);
        const parsedSubtitle = {
          index: index,
          subtitle: subtitle
        };
        resolve(parsedSubtitle);
      }).catch(res => {
        reject(res);
      });
    });
  }

  /**
   * 把 SRT 格式的字幕文件字符串解析为字幕的对象数组，格式为:
   * [
   *   {sn: "0", startTime: 0.89, endTime: 7.89, content: "不行就用。"},
   *   {sn: "1", startTime: 8.38, endTime: 14.85, content: "哎，汤姆，你这边的噪音待会儿看看能不能切掉，但是始终没有看到你人像。"}
   * ]
   * @param {*} srt
   */
  parseSrtSubtitle(res) {
    const srt = res.data;
    const subtitles = [];
    const srtTextArr = srt.split(/\n\n|\r\r|\r\n\r\n/).filter(item => item !== '');
    srtTextArr.forEach((item, index) => {
      let containArr = item.split(/\n/);
      const sn = containArr[0];
      const timeItem = containArr[1];
      if (timeItem) {
        const startTime = this.toSeconds(timeItem.split(' --> ')[0]); // 字幕的开始时间
        const endTime = this.toSeconds(timeItem.split(' --> ')[1]); // 字幕的结束时间
        let content = containArr[2] && containArr[2].trim(); // 字幕的内容 可能一行也可能两行
        if (containArr[3]) {
          content = `${content} <br/>${containArr[3].trim()}`;
        }
        const subtitle = {
          index: index,
          sn: sn,
          startTime: startTime,
          endTime: endTime,
          content: content
        };
        subtitles.push(subtitle);
      }
    });
    return subtitles;
  }

  /**
   * 把字符串格式的字幕时间转换为秒数
   *
   * @param {string} t 字符串格式的时间
   * @return s 秒数
   */
  toSeconds(t) {
    let sec = 0;
    const arr = t.split(',');
    const item = arr[0].split(':');
    sec = Number(item[0]) * 60 * 60 + Number(item[1]) * 60 + Number(item[2]) + arr[1] / 1000;
    return sec;
  }

  destroy() {
    super.destroy();
    removeFromParent(this.subtitleContainer);
  }

  static get type() {
    return 'plugin_subtitle';
  }
}
