import UIComponent from "../../common/UIComponent";
import SwitchComponent from "../switch/index.js"
import "./index.css";
import SubtitleList from "./subtitleList/index.js";
export default class ConfigBtn extends UIComponent {
  constructor(elementId, interfaces) {
    super();
    this.interfaces = interfaces;
    this.elementId = elementId;
    this.Lang = interfaces.language;
    this.init();

    this.interfaces.on(VhallPlayer.LIVESUBTITLE_OPENED, () => {
      let el = document.querySelector('.liveSubtitle > input');
      el.checked = !!el;
    });

    this.interfaces.on(VhallPlayer.LIVESUBTITLE_CLOSED, () => {
      let el = document.querySelector('.liveSubtitle > input');
      el.checked = !el;
    });
  }

  init() {
    this.configComponent = document.createElement("div");
    this.configComponent.setAttribute("class", "vhallPlayer-config-component");
    this.configComponent.onmouseleave = this.handleComponentMouseLeave.bind(this)

    this.configBtn = document.createElement("span");
    this.configBtn.setAttribute("class", "vhallPlayer-config-btn");
    this.configBtn.onclick = this.handleBtnClick.bind(this);

    this.configPopup = document.createElement("div");
    this.configPopup.setAttribute("class", "vhallPlayer-config-popup");

    this.configBox = document.createElement('div');
    this.configBox.setAttribute("class", "vhallPlayer-config-box");

    if (this.interfaces.isLive && this.interfaces.live_subtitle === 1) {
      // 直播字幕开关
      this.liveSubtitle = document.createElement("div");
      this.liveSubtitle.setAttribute("class", "vhallPlayer-config-item liveSubtitle");
      this.liveSubtitleText = document.createElement("span");
      this.liveSubtitleText.setAttribute("class", "liveSubtitletext");
      this.liveSubtitleText.innerText = this.Lang.subtitle.title;

      this.liveSubtitle.appendChild(this.liveSubtitleText);
      this.configBox.appendChild(this.liveSubtitle);
      this.configPopup.appendChild(this.configBox);
      this.configComponent.appendChild(this.configBtn);
      this.configComponent.appendChild(this.configPopup);

      this.insertElement(this.elementId, this.configComponent);

      new SwitchComponent(".vhallPlayer-config-item.liveSubtitle", this.interfaces, {
        text: "",
        id: "liveSubtitle",
        openCallback: this.openLiveSubtitle,
        closeCallback: this.closeLiveSubtitle
      });
      document.querySelector('.liveSubtitle > input').checked = true;
    } else if (!this.interfaces.isLive) {
      //循环开关
      this.configLoop = document.createElement("div");
      this.configLoop.setAttribute("class", "vhallPlayer-config-item loop");
      this.interfaces.isLive ? this.configLoop.classList.add("disabled") : undefined;
      this.configLoopText = document.createElement("span");
      this.configLoopText.setAttribute("class", "looptext");
      this.configLoopText.innerText = this.Lang.loop.title;

      this.configLoop.appendChild(this.configLoopText);
      this.configBox.appendChild(this.configLoop);

      // 字幕开关
      this.configSubtitle = document.createElement("div");
      this.configSubtitle.setAttribute("class", "vhallPlayer-config-item subtitle");
      this.interfaces.isLive ? this.configSubtitle.classList.add("disabled") : undefined;
      this.configSubtitleText = document.createElement("span");
      this.configSubtitleText.setAttribute("class", "subtitleText");
      this.configSubtitleText.innerText = this.Lang.subtitle.title;

      this.configSubtitle.appendChild(this.configSubtitleText);
      this.configBox.appendChild(this.configSubtitle);

      // 当前字幕文件
      this.currentSubtitle = document.createElement("div");
      this.currentSubtitle.setAttribute("class", "vhallPlayer-opensubtitle-item");

      this.currentText = document.createElement('span');
      this.currentText.setAttribute('class', 'vhallPlayer-current-subtitle');

      this.moreIcon = document.createElement('span');
      this.moreIcon.setAttribute('class', 'vhallPlayer-subtitle-rightIcon');
      this.moreIcon.onclick = this.handleMoreSubtitle.bind(this);

      this.currentSubtitle.appendChild(this.currentText);
      this.currentSubtitle.appendChild(this.moreIcon);
      this.configBox.appendChild(this.currentSubtitle);
      this.configPopup.appendChild(this.configBox);
      this.configComponent.appendChild(this.configBtn);
      this.configComponent.appendChild(this.configPopup);

      this.insertElement(this.elementId, this.configComponent);

      new SwitchComponent(".vhallPlayer-config-item.loop", this.interfaces, {
        text: "",
        id: "loop",
        openCallback: this.openLoop,
        closeCallback: this.closeloop
      });

      new SwitchComponent(".vhallPlayer-config-item.subtitle", this.interfaces, {
        text: "",
        id: "subtitle",
        openCallback: this.openSubtitle,
        closeCallback: this.closeSubtitle
      });

      const currSubtitle = this.interfaces.getCurrentSubtitle();
      const subtitleList = this.interfaces.getSubtitleList();
      new SubtitleList('.vhallPlayer-config-popup', this.interfaces, currSubtitle, subtitleList);

    }
  }

  openLoop() {
    this.interfaces.setLoop(true);
  }

  closeloop() {
    this.interfaces.setLoop(false);
  }

  openSubtitle() {
    // 开启字幕
    this.interfaces.openSubtitle();
    // 显示字幕文件
    const currentSubtitle = document.querySelector('.vhallPlayer-opensubtitle-item');
    currentSubtitle.style.display = 'flex';
    const currSubtitle = this.interfaces.getCurrentSubtitle();
    const currentText = document.querySelector('.vhallPlayer-current-subtitle');
    currentText.innerText = currSubtitle ? `字幕文件${currSubtitle.index + 1} - ${currSubtitle.lang}` : '暂无字幕文件';
    const subtitle = this.interfaces.getSubtitleList();
    if (subtitle && subtitle.length > 1) {
      const moreIcon = document.querySelector('.vhallPlayer-subtitle-rightIcon');
      moreIcon.style.display = 'inline-block';
    }
  }

  closeSubtitle() {
    // 关闭字幕
    this.interfaces.closeSubtitle();
    // 隐藏字幕文件
    const currentSubtitle = document.querySelector('.vhallPlayer-opensubtitle-item');
    currentSubtitle.style.display = 'none';
  }

  // 点击设置按钮
  handleBtnClick(e) {
    e.stopPropagation();
    this.configBtn.classList.add("active");
    this.configPopup.style.display = "block";
    this.configBox.style.display = 'block';

    // 当点播字幕显示时
    if (!this.interfaces.isLive && this.interfaces.vhallplayer.isOpenSubtitle) {
      document.querySelector('.subtitle > input').checked = true;
      this.currentSubtitle.style.display = 'flex';
      const currSubtitle = this.interfaces.getCurrentSubtitle();
      this.currentText.innerText = currSubtitle ? `字幕文件${currSubtitle.index + 1} - ${currSubtitle.lang}` : '暂无字幕文件';
      const subtitle = this.interfaces.getSubtitleList();
      if (subtitle && subtitle.length > 1) {
        this.moreIcon.style.display = 'inline-block';
      }
    }
  }

  handleComponentMouseLeave() {
    this.configBtn.classList.remove("active");
    this.configPopup.style.display = "none";
  }

  handleMoreSubtitle(e) {
    e.stopPropagation();
    this.configBox.style.display = "none";
    const subtitleListBox = document.querySelector(".vhallPlayer-subtitleList-box");
    subtitleListBox.style.display = "block";
  }

  openLiveSubtitle() {
    // 开启直播字幕
    this.interfaces.openLiveSubtitle();
  }

  closeLiveSubtitle() {
    // 关闭直播字幕
    this.interfaces.closeLiveSubtitle();
  }
}