import Plugin from '../core/Plugin';
import { PlayerEvent } from '../PlayerEvents';
import { KV } from '../core/Constant';
import { VHVideoConfig } from '../config';

/**
 * 直播暂停后播放逻辑优化插件
 *
 * @export
 * @class PlayCurrentTime
 * @extends {Plugin}
 */
export default class PlayCurrentTime extends Plugin {
  constructor() {
    super();
    this._allConfig = null;
  }

  init(opts = {}) {
    if (!opts.isLive) {
      this.info('info', '点播不运行暂停恢复逻辑');
      return;
    }

    super.init(opts);
    this._allConfig = opts;
    this._handleCareEvent();
  }

  _handleCareEvent() {
    this.player.on(PlayerEvent.PLAY, this._play.bind(this));
    this.player.on(PlayerEvent.PAUSE, this._pause.bind(this));
  }

  _pause() {
    this.pauseTime = Date.now();
  }

  _play() {
    if (!this.pauseTime) {
      // TODO 不自动播放时把进入的时间当暂停开始的时间，用于计算暂停时长
      // 但是重新拉流播最新的还是不自动播放，死循环无解，暂不考虑
      this.info('info', '直播开始时不运行暂停恢复逻辑');
      return;
    }

    // 开始时的自然时间, 用于计算暂停了多长时间
    this.playTime = Date.now();
    // 暂停时长
    const pauseDuration = (this.playTime - this.pauseTime) / 1000;
    this.pauseTime = null;
    this.playTime = null;
    let currentTime = this.player.getCurrentTime();

    // 【此处有坑】 bufferTime在安卓是Infinity
    // flash 返回的是数组
    const buffered = this.player.getBuffered();
    let bufferTime;
    if (this.player.player._type === 'FlashPlayer' && buffered.length > 0) {
      bufferTime = buffered[0][1];
    } else {
      bufferTime = buffered.length > 0 ? buffered.end(buffered.length - 1) : 0;
    }

    const pursueConfig = JSON.parse(this._allConfig.plugin_pursue);
    const vhVideoConfig = JSON.parse(VHVideoConfig.plugin_pursue);
    const type = this._allConfig.type;
    // 预留buffer不好用。
    let remain = 0;

    switch (type) {
      case 'flv':
        const flvRemain = pursueConfig['flv'] || vhVideoConfig['flv'];
        remain = flvRemain['remain'];
        break;
      case 'hls':
        const hlsRemain = pursueConfig['hls'] || vhVideoConfig['hls'];
        remain = hlsRemain['remain'];
        break;
      default:
        break;
    }

    if(type === 'rtc') {
      return;
    }

    const oldUrl = this.player.getStore().getKV(KV.URL);
    this.info('info', `oldUrl=${oldUrl}`);
    const baseUrl = oldUrl.split('?')[0];
    const token = this.player.newToken;
    let newUrl = `${baseUrl}?token=${token}`;
    // 时移直播暂停后 从暂停处开始播放
    if (this._allConfig.isLive && this._allConfig.isOpenTimeshift && this._allConfig.type === 'hls' && this.player.getCdnName() === 'tx' && this.player.player._type !== 'FlashPlayer') {
      const newDelay = this._player.player.delay + pauseDuration;
      // 短暂暂停从缓存播放
      if (isFinite(bufferTime) && bufferTime >= (currentTime + pauseDuration)) {
        this._player.player.delay += pauseDuration;
        this.info('info', '时移直播暂停恢复 -> 缓存接着播');
      } else {
        newUrl = `${baseUrl}?delay=${newDelay}&token=${token}}`;
        if (this._allConfig.x5 && this.player.player._type !== 'NativePlayer' && this.player.isFullscreen) {
          this.player.exitFullscreen();
          setTimeout(() => { this.player.setSrc(newUrl); }, 200);
          this._player.player.delay = newDelay;
        } else {
          // 暂停时间过长无buffer，重新拉流
          this.info('info', `暂停后delay=${newDelay}`);
          this.info('info', '时移直播暂停恢复 -> 重新拉流');
          this.player.setSrc(newUrl);
          this._player.player.delay = newDelay;
        }
        // 兼容ios
        this.player.play();
      }
    } else {
      // 正常直播
      if (this._allConfig.type === 'rtmp') {
        // 播rtmp没有buffer会自动跳到最新的
        this.info('info', '播rtmp没有buffer,点击播放会自动跳到最新的');
        return;
      }

      if (this._allConfig.type === 'hls' && this.player.player._type === 'FlashPlayer') {
        // IE播hls时 currentTime和 bufferTime不对 重新拉流
        this.info('info', 'IE播hls，暂停恢复 -> 重新拉流');
        this.player.setSrc(newUrl);
        return;
      }

      if (isFinite(bufferTime) && bufferTime >= (currentTime + pauseDuration)) {
        currentTime = currentTime + pauseDuration;
        this.info('info', `普通直播暂停恢复 -> 缓存 跳到最新 ${currentTime}`);
        if (currentTime < 1) {
          return;
        }
        // 【此处有坑】currentTime在移动端不能设为0
        // ios观看直播（nativePlayer）设置 currentTime 失效,仍然从暂停处开始播放
        this.player.setCurrentTime(currentTime);
      } else {
        if (this._allConfig.x5 && this.player.player._type !== 'NativePlayer' && this.player.isFullscreen) {
          this.player.exitFullscreen();
          setTimeout(() => { this.player.setSrc(newUrl); }, 200);
        } else {
          this.info('info', '普通直播暂停恢复 -> 重新拉流播最新的');
          // 【此处安卓有坑】 nativePlayer重设src，腾讯系还是播原来的
          this.player.setSrc(newUrl);
        }
        // 兼容ios
        this.player.play();
      }
    }
  }

  destroy() {
    super.destroy();
    this.player.off(PlayerEvent.PLAY, this._play);
    this.player.off(PlayerEvent.PAUSE, this._pause);
  }

  static get type() {
    return 'plugin_playCurrentTime';
  }
}
