import Plugin from '../core/Plugin';
import { PlayerEvent } from '../PlayerEvents';
import { removeFromParent, createElement, appendChild } from '../utils/Dom';
import { systemProbe } from '../utils/util';

/**
 * 移动端封面组件，PC端仍用video.poster属性
 *
 * @export
 * @class Poster
 * @extends {Plugin}
 */
export default class Poster extends Plugin {
  constructor() {
    super();
    this._allConfig = null;
    // 是pc还是mobile
    this.platform = systemProbe().platform;
  }

  init(opts = {}) {
    if (this.platform === 'pc' || !opts['poster']) {
      return;
    }
    super.init(opts);
    this._allConfig = opts;
    this._createPoster(opts);
    this._handleCareEvent();
  }

  _createPoster(opts) {
    this.poster = createElement('img', {
      id: 'vh-posterImg',
      src: `${opts['poster']}`
    }, {
      style: 'width: 100%; height: 100%; position: absolute; top: 0; left: 0; z-index: 0; object-fit: cover'
    });
    appendChild(opts['id'], this.poster);
  }

  _handleCareEvent() {
    this.player.on(PlayerEvent.PLAY, this.__onPlay.bind(this));
  }

  __onPlay() {
    this.poster.style.display = 'none';
  }

  static get type() {
    return 'plugin_poster';
  }

  destroy() {
    super.destroy();
    this.poster && removeFromParent(this.poster);
  }
}
