// import BarrageContainer from "./barrageContainer/index.js";
import UIComponent from "../common/UIComponent";
import PlayBtn from "./playBtn/index.js";
import CurrentTime from "./currentTime/index.js";
import DefinitionBtn from "./definitionBtn/index.js";
import SpeedBtn from "./speedBtn/index.js";
import TimeshiftProgress from "./timeshiftProgress/index.js";
import LiveShiftTime from "./liveShiftTime/index.js";
import Progress from "./progress/index.js";
import FullscreenBtn from "./fullscreenBtn/index.js";
import VolumeBtn from "./volumeBtn/index.js";
import ConfigBtn from "./configBtn/index.js";
import { debounce } from "../common/Utils";
import "./index.css";
import Bubble from "./bubble";

export default class Component extends UIComponent {
  constructor(elementId, interfaces, markPoints) {
    super();
    this.elementId = elementId;
    this.interfaces = interfaces;
    this.timer = null;

    this.markPoints = markPoints;

    this.init();

    // 声明当前播放器容器
    // @type HtmlElement
    this.UIContainer = document.querySelector(elementId);
    // 声明 当前控制条容器
    this.UIControlBar = this.UIMain;

    this.UIControlBar.style.display = this.UIContainer.offsetWidth <= 400 ? 'none' : 'block';

    window.addEventListener('resize', debounce(() => {
      this.UIControlBar.style.display = this.UIContainer.offsetWidth <= 400 ? 'none' : 'block';
    }, 100), false);


    let isMouseMove = false;
    this.UIContainer.addEventListener('mouseove', () => {
      isMouseMove = true;
      if (this.UIContainer.offsetWidth <= 400) return;
      this.UIControlBar.style.display = 'block';
      // this.timer = setTimeout(() => {
      // }, 5000);
    }, false);

    this.UIContainer.addEventListener('click', () => {
      // this.handleVideoClick.bind(this);
      this.UIControlBar.style.display = (this.UIControlBar.style.display == 'block' ? 'none': 'block');
    }, false);


    this.UIControlBar.addEventListener('click', (e) => {
      e.stopPropagation();
      e.preventDefault();
    }, false);
  }

  init() {
    this.UIMain = document.createElement("div");
    this.UIMain.setAttribute("class", "vhallPlayer-container");
    // this.UIMain.onclick = this.handleVideoClick.bind(this);
    this.UIMain.innerHTML = `
        <div class="vhallPlayer-progress-box"></div>
        <div class="vhallPlayer-controller-box">
          <div class="v-c-left"></div>
          <div class="v-c-right"></div>
        </div>
    `.trim();
    this.appendChild(this.elementId, this.UIMain);
    // new BarrageContainer(".vhallPlayer-container", this.interfaces);
    const isOpenTimeshift = this.interfaces.isLive ? this.interfaces.vhallplayer._config.isOpenTimeshift : null;
    const type = this.interfaces.vhallplayer._config.type;
    // flash不播时移，返回null，不显示时移UI
    const istx = (this.interfaces.getCdnName() === 'tx');



    const vCLeft = this.UIMain.querySelector('.v-c-left');
    const vCRight = this.UIMain.querySelector('.v-c-right');
    const vProcess = this.UIMain.querySelector('.vhallPlayer-progress-box');

    new PlayBtn(vCLeft, this.interfaces, this.UIMain);

    if (this.interfaces.isLive) {
      // 开启了 时移直播 - 才初始化时移直播相关参数
      if(isOpenTimeshift && type === 'hls' && istx) {
        new LiveShiftTime(vCLeft, this.interfaces)
        new TimeshiftProgress(vProcess, this.interfaces)
        new Bubble(this.UIMain, this.interfaces)
      }
    } else {
      new SpeedBtn(vCRight, this.interfaces)
      new CurrentTime(vCLeft, this.interfaces);
      new Progress(vProcess, this.interfaces, this.elementId, this.markPoints)
    }
    new DefinitionBtn(vCRight, this.interfaces);
    new VolumeBtn(vCRight, this.interfaces);
    new ConfigBtn(vCRight, this.interfaces);
    new FullscreenBtn(vCRight, this.interfaces);

  }
}