import Plugin from '../core/Plugin';
import LiveScheduler from './scheduler/LiveScheduler';
import VodScheduler from './scheduler/VodScheduler';
import LiveSubtitleScheduler from './scheduler/LiveSubtitleScheduler';
import {PlayerEvent} from "../PlayerEvents";

/**
 *
 * Distributed under the MIT license. See LICENSE file for details.
 * Copyright (c) 2019 vhall
 */

export default class Scheduler extends Plugin {
  constructor() {
    super();
    this._schedulerCfg = null;
    this._scheduler = null;
  }

  init(opts = '') {
    this._allConfig = opts;
    this._config = opts[Scheduler.type];
    this._schedulerCfg = JSON.parse(this._config);
    this._isLive = opts['isLive'];
    opts.live_subtitle = this._schedulerCfg['live_subtitle'];
    this._scheduler = this._isLive
      ? (this._schedulerCfg['live_subtitle'] === 1 ? new LiveSubtitleScheduler() : new LiveScheduler())
      : new VodScheduler();
    let originURL = document.createElement('a');
    originURL.href = this._allConfig.url;
    this._scheduler.uri = this._isLive ? '' : originURL.pathname;
    this._scheduler.type = this._allConfig.type;
    this._scheduler.player = this.player;
    this._scheduler.option = this._schedulerCfg;
    // hls内核和native内核都可以点播hls视频，目前只有hls内核可以解密
    if (!this._isLive && this.player.player._type === 'HlsPlayer' && this._schedulerCfg.cipher_key) {
      this.player.player.cipher_key = this._schedulerCfg.cipher_key;
    }
    if (this._isLive && opts && opts.liveOption && opts.liveOption.type === 'rtc') {
      this.player.emit2All(PlayerEvent.SCHEDULER_COMPLETE);
      this.player.emit2All(PlayerEvent.READY);
    } else {
      this._scheduler.fetch();
    }
  }

  static get type() {
    return 'plugin_scheduler';
  }
}
