
import "./index.css";
import "../common/iconfont/iconfont.css";
import UIComponent from "../common/UIComponent";
import H5FullScreenBtn from "./fullscreenBtn";
import H5PlayBtn from "./playBtn";
import H5Progress from "./h5progress";
import H5CurrentTime from "./h5currentTime";
import H5Duration from "./h5duration";
import H5LiveShiftTime from './h5liveshiftTime';
import H5TimeshiftProgress from './h5timeshiftProgress';
import Bubble from "./bubble";
import H5MiddlePlayBtn from "./h5MiddlePlayBtn";


/**
 * @description 初始化化  H5 移动端播放器相关控制功能
 * @author Sean.shi
 */
export default class H5Component extends UIComponent {
  constructor(elementId, interfaces, markPoints) {
    super();
    this.elementId = elementId;
    this.interfaces = interfaces;
    this.markPoints = markPoints
    this.init();
  }

  init() {
    // 创建 h5 控制条容器 shadow > UIMain > ...
    this.shadow = document.createElement("div");
    this.shadow.setAttribute("class", "vhallPlayerh5-shadow hide");
    this.UIMain = document.createElement("div");
    this.UIMain.setAttribute("class", "vhallPlayerh5-container");
    this.shadow.appendChild(this.UIMain);
    this.appendChild(this.elementId, this.shadow);
    // 兼容 恶心的 IOS 层级处理不合理问题 ！！！！
    document.querySelector(`${this.elementId} video`).style.position = 'relative';
    // pull shit code .

    // 是否开启了 时移直播功能
    const isOpenTimeshift = this.interfaces.isLive && this.interfaces.vhallplayer._config.isOpenTimeshift;
    // 直播类型
    const type = this.interfaces.vhallplayer._config.type;
    const istx = (this.interfaces.getCdnName() === 'tx');
    // 播放控件
    new H5PlayBtn(this.UIMain, this.interfaces);
    if(this.interfaces.isLive) {
      // 时移直播处理
      if(isOpenTimeshift && type === 'hls' && istx) {
        // 时移直播 - 控制条
        new Bubble(this.shadow, this.interfaces);
        new H5LiveShiftTime(this.UIMain, this.interfaces);
        new H5TimeshiftProgress(this.UIMain, this.interfaces, this.elementId);
      }
    } else {
      // 点播 - 初始化时间进度条
      new H5CurrentTime(this.UIMain, this.interfaces);
      // 点播 - 进度条
      new H5Progress(this.UIMain, this.interfaces, this.elementId, this.markPoints)
      // 点播 - 总时长
      new H5Duration(this.UIMain, this.interfaces);
    }
    // 全屏按钮 - 控件
    new H5FullScreenBtn(this.UIMain, this.interfaces);
    // 移动端居中暂停按钮
    new H5MiddlePlayBtn(this.elementId, this.interfaces);
  }
}
