import AbstractScheduler from './AbstractScheduler';
import Token from './Token';
import { PlayerEvent } from '../../PlayerEvents';
import { PropertyPriority } from '../../core/Constant';

/**
 * 带字幕直播调度策略
 *
 * @export
 * @class LiveSubtitleScheduler
 * @extends {AbstractScheduler}
 */
export default class LiveSubtitleScheduler extends AbstractScheduler {
  constructor() {
    super();
  }

  resolveData(data) {
    if (+data.code !== 200) {
      this.info('error', `获取直播状态码不为200，状态码为:${data.code}`);
      return;
    }

    // 创建token
    let token = new Token();
    token.player = this.player;
    token.create(data['data']['token']);

    // 测试用
    // data['data']['httpflv_subtitle_urls'] = data['data']['httpflv_urls'];
    // data['data']['hls_subtitle_urls'] = data['data']['hls_urls'];
    // data['data']['rtmp_subtitle_urls'] = data['data']['rtmp_urls'];
    // data['data']['timeshift']['hls_subtitle_urls'] = data['data']['timeshift']['hls_urls'];

    let allUrls = {};
    // 带字幕url列表
    let subtitleObj = null;
    // 不带字幕url列表
    let obj = null;
    let key = '';
    switch (this.type) {
      case 'flv':
        subtitleObj = data['data']['httpflv_subtitle_urls'];
        obj = data['data']['httpflv_urls'];
        key = 'httpflv_url';
        break;
      case 'hls':
        if (this.option.live_type === 1 && this.player.player._type !== 'FlashPlayer') {
          // 时移直播
          subtitleObj = data['data']['timeshift_subtitle']['hls_urls'];
          obj = data['data']['timeshift']['hls_urls'];
        } else {
          // 普通直播
          subtitleObj = data['data']['hls_subtitle_urls'];
          obj = data['data']['hls_urls'];
        }
        key = 'hls_url';
        break;
      case 'rtmp':
        subtitleObj = data['data']['rtmp_subtitle_urls'];
        obj = data['data']['rtmp_urls'];
        key = 'rtmp_url';
        break;
      default:
        this.info('warn', `暂时不支持${this.type}类型的直播`);
        return;
    }

    allUrls['1'] = subtitleObj;
    allUrls['0'] = obj;

    // 整理带字幕和不带字幕的线路
    let allDefList = {};
    for (let keyOf in allUrls) {
      let curObj = allUrls[keyOf];
      let allSupportDef = Object.keys(curObj);
      if (keyOf === '1' && allSupportDef.length < 1) {
        this.info('warn', '带字幕直播无清晰度可用');
        // return;
      }
      if (keyOf === '0' && allSupportDef.length < 1) {
        this.info('warn', '不带字幕直播无清晰度可用');
      }

      let lineCount = curObj[allSupportDef[0]].length;
      let defs = [];
      while (lineCount) {
        defs.push([]);
        lineCount -= 1;
      }

      Object.keys(curObj).forEach(item => {
        Object.values(curObj[item]).forEach((subItem, index) => {
          if (this.option['quality'].length && this.option['quality'].indexOf(item) === -1) {
            return;
          }
          let subDef = {
            idx: index,
            line: subItem['line'],
            is_subtitle: keyOf === '1',
            url: subItem[key],
            def: item,
            cdnName: subItem['cdn_name']
          };

          // TODO: 用flash看直播换清晰度不成功，所以优先播阿里CDN
          if (this.player.player._type === 'FlashPlayer' && this.type === 'rtmp' && subItem['cdn_name'] === 'ali') {
            this.priorityIndex = index;
          }

          // cdn_priority 自定义cdn
          if (this.option.cdn_priority && subItem['cdn_name'] === this.option.cdn_priority) {
            this.priorityIndex = index;
          }

          defs[index].push(subDef);
          defs[index].sort((a, b) => PropertyPriority[a.def] - PropertyPriority[b.def]);
        });
      });

      this.info(
        'info',
        `整理${keyOf === '1' ? '带' : '不带'}字幕直播清晰度完成,共有${defs.length}条线路，每条线路有${defs.length > 0 ? defs[0].length : 0}个清晰度`
      );

      // 如果存在自定义CDN, 将自定义CDN排在首位
      if (this.priorityIndex) {
        defs.forEach((value, index) => {
          if (defs[index][0]['idx'] === this.priorityIndex) {
            defs.splice(index, 1);
            defs.unshift(value);
          }
        });
      }

      allDefList[keyOf] = defs;
    }

    this._defineDefProperty(allDefList);
  }

  // defListObj的key含义：0不带字幕 1带字幕
  _defineDefProperty(defListObj) {
    // 所有带字幕和不带字幕清晰度列表
    let allDefinitionListObj = defListObj;
    // 所有带字幕清晰度列表
    let allSubtitleDefList = defListObj[`${this.option['live_subtitle']}`];
    // 默认选中列表中第一个
    let currentDefListIndex = 0;
    // 当前带字幕清晰度列表
    let currentSubtitleDefList = allSubtitleDefList[currentDefListIndex] || [];
    if (!this.option) {
      return;
    }

    currentSubtitleDefList = currentSubtitleDefList.sort((a, b) => PropertyPriority[a.def] - PropertyPriority[b.def]);

    let defaultDef = this.option['defaultDef'];
    // 备选方案, 设置的默认画质可能为480p， 但是清晰度列表中可能不存在480p， 找到一个备选清晰度。
    let alternatives = currentSubtitleDefList.filter(item => {
      return item.def !== 'a';
    });
    let alternativeDef = alternatives.length > 0 ? alternatives[0] : null;

    if (currentSubtitleDefList.length === 1) {
      alternativeDef = currentSubtitleDefList[0];
    }

    // 找到默认清晰度
    let defaultDefList = currentSubtitleDefList.filter(item => {
      return item.def === defaultDef;
    });

    let currentDefinition = defaultDefList.length ? defaultDefList[0] : alternativeDef;

    let properties = {
      currentDefinitionListIndex: currentDefListIndex,
      currentDefinitionList: currentSubtitleDefList,
      allDefinitionList: allDefinitionListObj
    };

    for (const key in properties) {
      Object.defineProperty(this.player, key, {
        configurable: true,
        get: function () {
          return properties[key];
        },
        set: function (newValue) {
          properties[key] = newValue;
        }
      });
    }

    // 因为这个要派发事件，所以单独拿出来
    Object.defineProperty(this.player, 'currentDefinition', {
      configurable: true,
      get: function () {
        return currentDefinition;
      },
      set: function (newValue) {
        const e = {
          oldValue: currentDefinition,
          newValue
        };
        let { url: oldURL } = currentDefinition;
        let { url: newURL } = newValue;
        if (oldURL === newURL) {
          this.info('info', '新旧线路相同');
          // 某些情况下（拉流失败等），卡顿通过切换清晰度逻辑进行容错。故清晰度相同也切
          // return;
        }
        this.info('info', '切换线路');
        this.info('info', `old: ${currentDefinition.url}`);
        this.info('info', `new: ${newValue.url}`);
        currentDefinition = newValue;
        if (newValue.noSet) {
          delete newValue.noSet;
          return;
        }
        let token = this.newToken;
        let url = `${newValue.url}?token=${token}`;
        // 时移直播
        if (this._config.isLive && this._config.isOpenTimeshift && this._config.type === 'hls' && this.player.getCdnName() === 'tx') {
          url = `${newValue.url}?delay=${this.player.delay}&token=${token}`;
        }

        this.player.setSrc(url);
        this.player.emit2All(PlayerEvent.DEFINITION_CHANGED, e);
      }
    });

    // 带字幕直播默认开启
    this.player.player._isOpenedLiveSubtitle = true;
    this.player.emit2All(PlayerEvent.LIVESUBTITLE_OPENED);
    // 直播字幕调度完成
    this.player.emit2All(PlayerEvent.SCHEDULER_COMPLETE);
    this.player.emit2All(PlayerEvent.READY);
  }
}
