import qs from 'qs';
import 'formdata-polyfill';
import { systemProbe } from '../../utils/util';

/**
 * 简单封装
 *
 * @export
 * @class Http
 */
export default class Http {
  constructor(store) {
    this.store = store;
    this.e = {
      error: this._onError.bind(this),
      timeout: this._onTimeout.bind(this),
      onload: this._onLoad.bind(this),
      onLoadend: this._onLoadend.bind(this)
    };

    this.xhr = new XMLHttpRequest();
    // IE, 不支持赋值
    this.browserType = systemProbe().browser;
    if (this.browserType !== 'ie') {
      this.xhr.responseType = 'json';
      this.xhr.timeout = 5000;
    }
    this.xhr.onerror = this.e.error;
    this.xhr.ontimeout = this.e.timeout;
    this.xhr.onload = this.e.onload;
    this.xhr.onloadend = this.e.onLoadend;

    this.onError = null;
    this.onLoad = null;
    this.onLoadend = null;
    this.onTimeout = null;
  }

  /**
   * inner method
   *
   * @param {*} e
   * @memberof Http
   */
  _onError(e) {
    this.onError && this.onError();
    this.onError = null;
  }

  /**
   * inner method
   *
   * @param {*} e
   * @memberof Http
   */
  _onLoad(e) {
    if (!e.target) {
      this._onError(e);
      return;
    }

    if (!e.target.response) {
      this._onError(e);
      return;
    }

    let response = e.target.response;
    // IE, 由于未赋值responseType为JSON，故手动解析
    if (this.browserType === 'ie' && response && typeof response === 'string') {
      try {
        response = JSON.parse(response);
      } catch (error) {
        response = e.target.response;
      }
    }
    let { code, data } = response;
    if (+code !== 200) {
      this._onError(e);
      return;
    }

    this.onLoad && this.onLoad(data);
    this.onLoad = null;
  }

  /**
   * inner method
   *
   * @param {*} e
   * @memberof Http
   */
  _onLoadend(e) {
    this.onLoadend && this.onLoadend(e.target);
    this.onLoadend = null;
  }

  /**
   * inner method
   *
   * @param {*} e
   * @memberof Http
   */
  _onTimeout(e) {
    this.onTimeout && this.onTimeout();
    this.onTimeout = null;
  }

  /**
   * 发送请求
   *
   * @param {*} url API的URL
   * @param {*} data FormData
   * @param {string} [method='POST']
   * @memberof Http
   */
  fire(url, data = {}, method = 'GET') {
    if (!this.xhr) {
      return;
    }

    // 构建formData数据
    // FormData polyfill 的类
    let formData = new FormData();

    Object.keys(data).forEach(keys => {
      formData.set(keys, data[keys]);
    });

    if (method === 'POST') {
      this.xhr.open(method, url);
      this.xhr.setRequestHeader('Content-Type','application/json')
      this.xhr.send(JSON.stringify(data));
    } else if (method === 'GET') {
      url += `?${qs.stringify(data)}`;
      this.xhr.open(method, url);
      this.xhr.send();
    }
  }

  /**
   * HTTP模块销毁
   *
   * @memberof Http
   */
  destroy() {
    if (!this.xhr) {
      return;
    }

    this.xhr.onerror = null;
    this.xhr.onload = null;
    this.xhr.onloadend = null;
    this.xhr.ontimeout = null;
    this.xhr = null;

    this.onLoad = null;
    this.onLoadend = null;
    this.onTimeout = null;
    this.onError = null;
  }
}
