import Plugin from '../core/Plugin';
import { PlayerEvent } from '../PlayerEvents';

/**
 * 切线业务逻辑
 * 接收卡顿与播放器报错，二者皆有的情况下再切线
 *
 * @export
 * @class thorn
 * @extends {Plugin}
 */
export default class Switcher extends Plugin {
  constructor() {
    super();
    // 所有线路列表
    this.Lines = null;
    // 当前线路索引
    this.currentLineIndex = 0;
    // 切线定时器
    this._changeLineTimeout = 0;
    // 卡顿计数
    this.lagCount = 0;
    // 播放器拉流失败报错记录
    this.playerError = false;
  }

  init(opts = {}) {
    super.init(opts);
    this._allConfig = opts;
    this.thornConfig = JSON.parse(opts[Switcher.type]);

    if (!this.thornConfig['enable']) {
      this.info('info', '未启用卡顿切线功能');
      return;
    }

    this._handleCareEvent();
  }

  _handleCareEvent() {
    // 调度完成
    this.player.on(PlayerEvent.SCHEDULER_COMPLETE, this.__schedulerCompleted.bind(this));
    // 卡顿
    this.player.on('lagreport', this.__lag.bind(this));
    // 卡顿恢复
    this.player.on('lagrecover', this.__lagRecover.bind(this));
    // 连接失败
    this.player.on('connectError', this.__connectError.bind(this));
    this.player.on(PlayerEvent.CHANGE_LINE, this.__changeLine.bind(this)); // 直播结束后抛出的切线尝试
    this.player.on(PlayerEvent.ERROR, this.__error.bind(this));
    this.player.on(PlayerEvent.STALLED, this.__error.bind(this));
  }

  __lag() {
    this.lagCount += 1;
    if (this.lagCount < 3 || !this.playerError) {
      this.info('info', '卡顿次数+1');
      return;
    }

    // 卡顿后开始计时，超过多久过后启动换线流程
    this.info('info', '准备卡顿切线');
    this.__changeLine(); // 还未开播拉流重试、卡顿重试
  }

  __error(e) {
    this.info('info', '播放器报错');
    if (e && e.type === 'mediaError' && (e.details === 'bufferStalledError' || e.details === 'bufferNudgeOnStall')) {
      this.info('info', '忽略');
      return;
    }

    this.playerError = e;
    if (this.lagCount > 3) {
      this.__changeLine();
    }
  }

  __lagRecover(e) {
    this._changeLineTimeout && clearTimeout(this._changeLineTimeout);
  }

  __connectError(e) {
    this._changeLineTimeout && clearTimeout(this._changeLineTimeout);
    const threshold = this._allConfig.switchLineThreshold * 1000;
    this._changeLineTimeout = setTimeout(this.__changeLine.bind(this), threshold);
  }

  __schedulerCompleted() {
    this.info('info', '卡顿切换策略--->build');
    this.Lines = this.player.allDefinitionList;
    this.info('info', '卡顿切换策略--->build complete');
  }

  async __changeLine() {
    this._changeLineTimeout && clearTimeout(this._changeLineTimeout);
    let newDefObj;
    let oldDef = this.player.currentDefinition.def;
    if (this._allConfig.isLive && this._allConfig.live_subtitle === 1) {
      // 带字幕直播
      if (this.player.player._isOpenedLiveSubtitle) {
        // 带字幕=>切到无字幕列表第一个
        if (!this.player.allDefinitionList['0'] || this.player.allDefinitionList['0'].length === 0) {
          this.info('info', 'There is no any line without subtitle');
          // todo 往带字幕其它线切
        }
        this.currentLineIndex = 0;
        this.player.currentDefinitionListIndex = this.currentLineIndex;
        this.player.currentDefinitionList = this.player.allDefinitionList['0'][this.currentLineIndex];
        this.player.emit2All(PlayerEvent.LIVESUBTITLE_CLOSED);
        this.player.player._isOpenedLiveSubtitle = false;
      } else {
        // 无字幕=>切无字幕
        this.currentLineIndex += 1;
        if (this.currentLineIndex >= this.player.allDefinitionList['0'].length) {
          this.currentLineIndex = 0;
        }
        this.player.currentDefinitionListIndex = this.currentLineIndex;
        this.player.currentDefinitionList = this.player.allDefinitionList['0'][this.currentLineIndex];
      }
    } else {
      // 无字幕直播
      if (this.Lines === null || this.Lines.length === 0) {
        this.info('error', 'no available line');
        return;
      }

      this.currentLineIndex += 1;
      if (this.currentLineIndex >= this.Lines.length) {
        this.currentLineIndex = 0;
      }
      this.player.currentDefinitionListIndex = this.currentLineIndex;
      this.player.currentDefinitionList = this.Lines[this.currentLineIndex];
    }

    let arr = this.player.currentDefinitionList;
    for (let i = 0; i < arr.length; i++) {
      if (arr[i]['def'] === oldDef) {
        newDefObj = arr[i];
        break;
      }
    }

    this.lagCount = 0;
    this.playerError = false;

    // 判断token是否过期，如果过期，获取新的token
    if (this.player.tokenIsExpire()) {
      this.info('info', 'token过期，获取新token');
      await this.player.fetchToken();
    }

    // 手机全屏状态下切线会自动退出全屏，并且无法再次进入！
    if (this._allConfig.x5 && this.player.player._type !== 'NativePlayer' && this.player.isFullscreen) {
      this.info('info', '手动退出全屏');
      // 退出全屏后不能马上切线，不然还是无法进入全屏。。。
      this.player.exitFullscreen();
      setTimeout(() => { this.player.currentDefinition = newDefObj; }, 200);
    } else {
      this.player.currentDefinition = newDefObj;
    }
  }

  destroy() {
    super.destroy();
    this._changeLineTimeout && clearTimeout(this._changeLineTimeout);

    this.player = null;
  }

  static get type() {
    return 'plugin_thorn';
  }
}
