import UIComponent from "../../common/UIComponent";
import "./index.css";

export default class markPoints extends UIComponent {
    constructor(domNode, interfaces, markPoints) {
        super();
        this.interfaces = interfaces;
        this.node = document.querySelector(domNode);
        this.markPoints = markPoints;

        this.interfaces.on(VhallPlayer.LOADED, () => {

            let length = this.interfaces.getDuration();
            
            let width = this.node.offsetWidth;

            let minPercent = 80 / width;
            minPercent = minPercent * 100; 
            let maxPercent = 100 - minPercent;

            // 转换为百分比            
            maxPercent = maxPercent;


            this.markPoints = this.markPoints.map((item) => {
                return {
                    style: {
                        top: 0,
                        left: `${(item.timePoint / length) * 100}`
                    },
                    ...item
                }
            });
            this.markPoints.length && this.init(minPercent, maxPercent)
        })
    }

    init (minPercent, maxPercent) {
      const url = this.interfaces.vhallplayer.getStore().getKV('url');
      const head = url.split('//')[0];
      const domain = url.split('//')[1].split('/')[0];
        this.markPoints.forEach((element, idx) => {
            let i = document.createElement('div');
            i.style.cssText = `left: ${element.style.left}%;`;
            i.setAttribute('class', 'v-p-markpoint');

            let mark = document.createElement('div');
            mark.setAttribute('class', 'v-p-markpoint__mark');

            const markMsg = document.createElement('div');
            markMsg.setAttribute('class', 'v-p-markpoint-msg');
            markMsg.innerText = element.msg;
            mark.appendChild(markMsg);

            if (element.picurl) {
              const markImg = document.createElement('img');
              markImg.setAttribute('src', `${head}//${domain}${element.picurl}`);
              markImg.setAttribute('alt', ' ');
              mark.appendChild(markImg);
            }

            let markPercent = element.style.left;

            (markPercent < minPercent) && (markPercent = minPercent);
            (markPercent > maxPercent) && (markPercent = maxPercent);

            mark.style.cssText = `left: ${markPercent}%; display: none`;

            this.node.appendChild(i);
            this.node.appendChild(mark);

            i.addEventListener('click', (e) => {
                this.interfaces.setCurrentTime( +element.timePoint)
                e.preventDefault()
                e.stopPropagation()
            }, false);


            i.addEventListener('mouseover', (e) => {
                mark.cssText = `left: ${markPercent}%; display: block;`;
                mark.style.display = 'block';
                e.stopPropagation()
            }, false)
            // 阻止事件冒泡，触发 滚动条的拖动事件
            i.addEventListener('mousedown', (e) => {
                e.preventDefault()
                e.stopPropagation()
            }, false)

            // 阻止事件冒泡，触发 滚动条的拖动事件
            i.addEventListener('touchstart', (e) => {
                this.interfaces.setCurrentTime( +element.timePoint)
                e.stopPropagation()
            }, false)

            i.addEventListener('mouseout', (e) => {
                mark.style.display = 'none'
                e.preventDefault()
                e.stopPropagation()
            }, false)

        });
        

    }

    createPointNode(txt, position) {
        //
    }
}