import { VHVideoConfig } from './config';
import Component from './core/Component';
import Store from './core/Store';
import { AutoDetectInitial, UserCustomInitial, FlashInitial } from './player/createPlayer';
import { PlayerEvent } from './PlayerEvents';
import PluginMap from './plugins/PluginMap';
import Log from './utils/Log';
import { systemProbe } from './utils/util';

const TENCENT_DELAY = 90;

/**
 * 播放器模块
 *
 * @export
 * @class VideoModule
 * @extends {Component}
 */
export default class VideoModule extends Component {
  constructor() {
    super();
    this.player = null; // FlvPlayer | HlsPlayer | NativePlayer | FlashPlayer
    this._config = {};
    this.store = new Store();
    this.pluginInstance = [];
    Log.OBJ.level = process.env.LOG_LEVEL;
    // 探针判断系统环境
    this.context = systemProbe();
  }

  async init(option = {}) {
    let initializer;
    let config = this._configMapping(option);
    this._config = config;
    // 如用户未传入，则默认判断逻辑，否则按照用户传入处理
    if (config.useSWF) {
      initializer = new FlashInitial(this);
    } else if (config.type === 'auto' && config.isLive) {
      initializer = new AutoDetectInitial(this);
    } else {
      initializer = new UserCustomInitial(this);
    }

    this.player = await initializer.init(this.context, config);
    this.initPluginListener();
    this._pluginCall();
  }

  _configMapping(option = {}) {
    let config = {};
    if (option['plugin_timeshift']) {
      const timeshiftConfig = JSON.parse(option['plugin_timeshift']);
      option.isOpenTimeshift = timeshiftConfig['isOpenTimeshift'];
      option.live_duration = timeshiftConfig['live_duration'];
    }
    if (option['isLive']) {
      option.useSWF = option['liveOption'] && option['liveOption']['useSWF'];
      option.forceMSE = option['liveOption'] && option['liveOption']['forceMSE'];
    } else {
      option.useSWF = option['vodOption'] && option['vodOption']['useSWF'];
      option.forceMSE = option['vodOption'] && option['vodOption']['forceMSE'];
    }
    // 是否使用flash，如用户未传入，则默认判断逻辑，否则按照用户传入处理
    option.useSWF =
      option.useSWF === undefined ? this.context.browser === 'ie' || config.type === 'rtmp' : option.useSWF;
    // 是否强制使用MSE，如用户未传入，则默认判断逻辑，否则按照用户传入处理
    // option.forceMSE = option.forceMSE === undefined ? this.context['supportMSE'] === true && !(this.context['os'] === 'android' && this.context['browser'] === 'qqbrowser') : option.forceMSE;
    this.info('info', `是否支持MSE: ${this.context.supportMSE}; 是否强制使用MSE: ${option.forceMSE}`);
    this.info('info', `useSWF: ${option.useSWF}`);
    Object.assign(config, VHVideoConfig, option);
    switch (config.type) {
      case 'flv':
        config.url = option.flvurl;
        config.lazyLoadMaxDuration = VHVideoConfig.maxBufferTime;
        break;
      case 'hls':
        config.url = option.hlsurl;
        break;
      case 'native':
        config.url = option.nativeurl;
        break;
      default:
        break;
    }
    return config;
  }

  _pluginCall() {
    if (!this.player) {
      this.info('warn', '播放器为空，无法初始化插件');
      return;
    }
    PluginMap.forEach(clazz => {
      // eslint-disable-next-line new-cap
      let cl = new clazz();
      cl.player = this;
      cl.init(this._config);
      this.pluginInstance.push(cl);
    });
  }

  destroy() {
    super.destroy();

    this.removeAllEvents();
    // 清理插件
    this.pluginInstance.forEach(plugin => {
      plugin.destroy();
    });

    this.pluginInstance = [];
    if (this.player) {
      this.player.destroy();
      this.player = null;
    }
  }

  setSize(w, h) {
    const parent = this.player.getRoot();
    if (!parent) {
      Log.OBJ.warn('无法找到root元素');
      return;
    }

    parent.style.width = w;
    parent.style.height = h;
    this.pluginInstance.forEach(plugin => {
      plugin.setSize(w, h);
    });
  }

  initPluginListener() {
    const self = this;
    this.on(PlayerEvent.SCHEDULER_COMPLETE, () => {
      const def = self.currentDefinition || {};
      this.info('info', `当前清晰度${JSON.stringify(def)}`);
      const token = self.newToken;
      let url = `${def.url}?token=${token}`;
      this.player.cdnName = def.cdnName;
      // 腾讯cdn时移直播
      if (this._config.isLive && this._config.isOpenTimeshift && this._config.type === 'hls' && this.player.cdnName === 'tx') {
        url = `${def.url}?delay=${TENCENT_DELAY}&token=${token}`;
        this.player.delay = TENCENT_DELAY;
      }
      this.player.setSrc(url);
      if (this._config.autoplay && this.context['os'] === 'ios') {
        this.play().then(() => {
          this.info('info', '支持自动播放');
        }).catch(() => {
          this.info('info', '自动播放被禁止');
          this.emit2All(PlayerEvent.AUTOPLAY_FAILED);
        });
      }
    });
  }

  async setQuality(newValue) {
    if(this.player.rtc && this.player.rtc.setVideoProfile) {
      let streamType = '1';
      if (newValue.def === 'high') {
        streamType = this.player.rtc.Constant.RTC_BIG_STREAM;
      } else if (newValue.def === 'low') {
        streamType = this.player.rtc.Constant.RTC_SMALL_STREAM;
      }
      this.player.rtc.setVideoProfile({type: streamType}).then(() => {
        this.player.emit2All(PlayerEvent.DEFINITION_CHANGED, {
          oldValue: {},
          newValue
        });
        Log.OBJ.info('大小流切换成功');
      }).catch((e) => {
        Log.OBJ.info('大小流切换失败');
      })
      return
    }
    const e = {
      oldValue: JSON.parse(JSON.stringify(this.currentDefinition)),
      newValue
    };
    let token = this.newToken;
    let url = `${newValue.url}?token=${token}`;
    this.currentDefinition = { noSet: true, ...newValue };
    // 时移直播
    if (this._config.isLive && this._config.isOpenTimeshift && this._config.type === 'hls' && this.player.getCdnName() === 'tx') {
      url = `${newValue.url}?delay=${this.player.delay}&token=${token}`;
    }

    this.player.isChangeDefinition = true;
    // 点播记录换清晰度时的时间,用于判断是否向文档派发timeupdate事件
    if (!this._config.isLive && !this.player.vodPlayedTime) {
      this.player.vodPlayedTime = this.player.getCurrentTime();
    }
    this.player.emit2All(PlayerEvent.DEFINITION_CHANGED, e);
    try {
      await this.player.setSrc(url);
      // 手动换清晰度在这里会重复seek,封装的内核里已经seek了一次。
      this.player.vodPlayedTime && this.player.setCurrentTime(this.player.vodPlayedTime);
    } catch (error) {
      Log.OBJ.warn('切源失败');
    }
  }

  get version() {
    return process.env.VERSION;
  }
}
