import Plugin from '../core/Plugin';
import { PlayerEvent } from './../PlayerEvents';

/**
 * 直播字幕插件
 *
 * @export
 * @class LiveSubtitle
 * @extends {Plugin}
 */

export default class LiveSubtitle extends Plugin {
  constructor() {
    super();
  }

  static get type() {
    return 'plugin_liveSubtitle';
  }

  init(opts = {}) {
    if (!opts.isLive || opts.live_subtitle !== 1) {
      return;
    }
    super.init(opts);
    this._allConfig = opts;
    this._isOpenTimeshift = opts.isOpenTimeshift;
    this._type = opts.type;
    this._isLive = opts.isLive;
    this._defineMethods();
  }

  _defineMethods() {
    this.player.openLiveSubtitle = this._openLiveSubtitle.bind(this);
    this.player.closeLiveSubtitle = this._closeLiveSubtitle.bind(this);
  }

  _openLiveSubtitle() {
    let defObj = this.player.currentDefinition;
    if (!defObj || !this._isLive || this._allConfig.live_subtitle !== 1) {
      return;
    }
    let isSubtitle = defObj.is_subtitle;
    if (isSubtitle) {
      this.info('info', '直播字幕已经开启');
      return;
    }
    // 切到带字幕列表第一个(直播字幕不支持纯音频,默认切到原画)
    let oldDef = defObj.def;
    let newDefObj;
    this.currentLineIndex = 0;
    this.player.currentDefinitionListIndex = this.currentLineIndex;
    this.player.currentDefinitionList = this.player.allDefinitionList['1'][this.currentLineIndex];
    let arr = this.player.currentDefinitionList;
    for (let i = 0; i < arr.length; i++) {
      if (arr[i]['def'] === oldDef) {
        newDefObj = arr[i];
        break;
      }
    }

    if (!newDefObj) {
      newDefObj = arr[0];
    }

    this.player.player._isOpenedLiveSubtitle = true;
    this.player.emit2All(PlayerEvent.LIVESUBTITLE_OPENED);
    // 手机全屏状态下切线会自动退出全屏，并且无法再次进入！
    if (this._allConfig.x5 && this.player.player._type !== 'NativePlayer' && this.player.isFullscreen) {
      this.info('info', '手动退出全屏');
      // 退出全屏后不能马上切线，不然还是无法进入全屏。。。
      this.player.exitFullscreen();
      setTimeout(() => { this.player.currentDefinition = newDefObj; }, 200);
    } else {
      this.player.currentDefinition = newDefObj;
    }
  }

  _closeLiveSubtitle() {
    let defObj = this.player.currentDefinition;
    if (!defObj || !this._isLive || this._allConfig.live_subtitle !== 1) {
      return;
    }
    let isSubtitle = defObj.is_subtitle;
    if (!isSubtitle) {
      this.info('info', '直播字幕已经关闭');
      return;
    }
    // 切到不带字幕列表第一个
    let oldDef = defObj.def;
    let newDefObj;
    this.currentLineIndex = 0;
    this.player.currentDefinitionListIndex = this.currentLineIndex;
    this.player.currentDefinitionList = this.player.allDefinitionList['0'][this.currentLineIndex];
    let arr = this.player.currentDefinitionList;
    for (let i = 0; i < arr.length; i++) {
      if (arr[i]['def'] === oldDef) {
        newDefObj = arr[i];
        break;
      }
    }

    if (!newDefObj) {
      newDefObj = arr[0];
    }

    this.player.player._isOpenedLiveSubtitle = false;
    this.player.emit2All(PlayerEvent.LIVESUBTITLE_CLOSED);
    // 手机全屏状态下切线会自动退出全屏，并且无法再次进入！
    if (this._allConfig.x5 && this.player.player._type !== 'NativePlayer' && this.player.isFullscreen) {
      this.info('info', '手动退出全屏');
      // 退出全屏后不能马上切线，不然还是无法进入全屏。。。
      this.player.exitFullscreen();
      setTimeout(() => { this.player.currentDefinition = newDefObj; }, 200);
    } else {
      this.player.currentDefinition = newDefObj;
    }
  }
}
